html {
  box-sizing: border-box;
  font-size: 100%;
}
@media only screen and (min-width: 768px) {
  html {
    font-size: 112.5%;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

.container {
  background-color: #b2b2b2;
  width: 100%;
  height: 100%;
}

.grid-container {
  margin: 0 auto;
  max-width: 2732px;
  background-color: #fff;
  display: grid;
  grid-template-columns: [main-start] 1fr [main-end];
}
@media only screen and (min-width: 768px) {
  .grid-container {
    grid-template-columns: [main-start] 1fr [main-middle] 1fr [main-end];
  }
}
@media only screen and (min-width: 1024px) {
  .grid-container {
    grid-template-columns: min-content [main-start] 1fr [main-middle] 1fr [main-end];
  }
}

html {
  font-family: "Open Sans", sans-serif;
  color: #6e6e6e;
}

h1 {
  font-weight: 700;
}

h2,
h3 {
  font-weight: 400;
}

h1 {
  font-size: 1.3rem;
}

h2 {
  font-size: 1.2rem;
}

h3 {
  font-size: 1.1rem;
  text-transform: uppercase;
}

.alert {
  grid-column: main-start/main-end;
  margin: 1rem 0.5rem;
  padding: 0.5rem;
  border-radius: 5px;
  background-color: #7477bf;
  color: #fff;
  font-size: 0.8rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.alert__title {
  font-weight: 700;
  margin-right: 0.5rem;
}
.alert__close {
  font-weight: 700;
  font-size: 1rem;
  padding: 0 0.5rem;
  margin-left: 0.5rem;
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
  .alert {
    margin: 1rem 1rem;
  }
}

.btn {
  font-family: inherit;
  border: 1px solid #b2b2b2;
  padding: 0.5rem;
  border-radius: 5px;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}

.switch {
  font-size: 13px;
  width: 70%;
  max-width: 450px;
  display: flex;
  flex-direction: column;
}
.switch__input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch__label {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.switch__text {
  margin-right: 10px;
}
.switch__button {
  position: relative;
  cursor: pointer;
  width: 80px;
  height: 32px;
  border: 1px solid #b2b2b2;
  border-radius: 34px;
  background-color: #b2b2b2;
  transition: 0.4s;
}
.switch__button::before {
  position: absolute;
  content: "";
  height: 24px;
  width: 24px;
  left: 3px;
  bottom: 3px;
  border: 1px solid #b2b2b2;
  border-radius: 50%;
  background-color: #fff;
  transition: 0.4s;
}
.switch__input:checked + .switch__label .switch__button {
  background-color: #7477bf;
}
.switch__input:checked + .switch__label .switch__button::before {
  transform: translateX(47px);
}
.switch__button::after {
  position: absolute;
  content: "ON";
  left: 10px;
  bottom: 6px;
  color: #fff;
  font-size: 14px;
  opacity: 0;
  transform: translateX(-47px);
  transition: 0.4s;
}
.switch__input:checked + .switch__label .switch__button::after {
  opacity: 1;
  transform: translateX(0);
}

.drop-down {
  width: 100%;
  padding: 0.5rem;
  color: #b2b2b2;
  background-color: #fbfbfb;
  border: 1px solid #b2b2b2;
  border-radius: 5px;
}

.member {
  display: flex;
  align-items: center;
  padding-bottom: 0.8rem;
  font-size: 0.8rem;
  /*************************************
    Optional Elements
  *************************************/
}
@media only screen and (min-width: 320px) {
  .member {
    font-size: 0.9rem;
  }
}
@media only screen and (min-width: 768px) {
  .member {
    font-size: 0.6rem;
  }
}
.member__photo {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 0.5rem;
}
.member__info {
  flex: 1;
}
.member__name {
  margin-bottom: -3px;
}
.member:not(:first-child) {
  padding-top: 0.8rem;
}
.member:not(:last-child) {
  border-bottom: 1px solid #b2b2b2;
}
.member__email:link, .member__email:visited {
  color: #7477bf;
  text-decoration: none;
}
.member__arrow {
  font-size: 1.8rem;
  color: #b2b2b2;
  font-weight: 300;
  line-height: 0.8;
}

.header {
  padding: 0.7rem 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #7477bf;
  color: #fff;
  grid-column: 1/-1;
}
.header__title span {
  font-weight: 300;
}
@media only screen and (min-width: 768px) {
  .header {
    padding: 0.7rem 1rem;
  }
}
@media only screen and (min-width: 1024px) {
  .header {
    grid-row: 1/2;
  }
}

.user {
  display: flex;
  align-items: center;
  line-height: 2;
  font-weight: 300;
  height: 2rem;
}
.user :not(:last-child) {
  margin-right: 0.5rem;
}
.user__notification {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
}
.user__bell {
  height: 1.3rem;
  width: 1.3rem;
}
.user__dot {
  position: absolute;
  width: 6px;
  height: 6px;
  top: -4px;
  right: 6px;
  background-color: #74b1bf;
  border-radius: 50%;
}
.user__photo {
  display: flex;
  border-left: 1px solid #4d4c72;
  padding-left: 0.5rem;
}
.user__photo img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.notifications {
  background: #fff;
  grid-column: main-start/main-end;
}
.notifications__list {
  list-style-type: none;
}
.notifications__item {
  padding: 0.5rem;
  flex-wrap: wrap;
  display: none;
  align-items: center;
  transition: background-color 0.4s;
}
.notifications__item:hover {
  background-color: #b2b2b2;
}
.notifications__title {
  font-weight: 700;
  margin-right: 0.5rem;
  width: 100%;
}
.notifications__message {
  flex: 1;
}
.notifications__close {
  margin-left: 0.5rem;
  font-weight: 700;
  font-size: 1.5rem;
  cursor: pointer;
}

.sidebar {
  background: #4d4c72;
  grid-column: 1/-1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.sidebar__item {
  cursor: pointer;
  position: relative;
  opacity: 0.5;
  transition: opacity 0.3s;
}
.sidebar__item--selected, .sidebar__item:hover {
  opacity: 1;
}
.sidebar__item--selected::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #81c98f;
}
.sidebar__item-link {
  display: block;
  padding: 2rem;
}
@media only screen and (min-width: 768px) {
  .sidebar__item-link {
    padding: 1.5rem;
  }
}
@media only screen and (min-width: 1024px) {
  .sidebar__item-link {
    padding: 1rem;
  }
}
.sidebar__icon {
  height: 1.8rem;
  width: 1.8rem;
}
@media only screen and (min-width: 768px) {
  .sidebar__icon {
    height: 1.5rem;
    width: 1.5rem;
  }
}
@media only screen and (min-width: 1024px) {
  .sidebar__icon {
    height: 1.3rem;
    width: 1.3rem;
  }
}
@media only screen and (min-width: 1024px) {
  .sidebar {
    grid-column: 1/2;
    grid-row: 2/10;
    flex-direction: column;
    justify-content: flex-start;
  }
  .sidebar__item--selected::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    height: 100%;
    width: 2px;
    background-color: #81c98f;
  }
}

.title-bar {
  grid-column: main-start/main-end;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}
.title-bar__title {
  padding: 1rem 0.5rem;
  flex: 1;
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .title-bar__title {
    width: auto;
    padding: 1rem 1rem;
  }
}
@media only screen and (min-width: 768px) {
  .title-bar {
    flex-wrap: nowrap;
    flex-direction: row;
  }
}
@media only screen and (min-width: 1024px) {
  .title-bar {
    grid-row: 2/3;
  }
}

.search {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fbfbfb;
  border-top: 1px solid #b2b2b2;
  border-bottom: 1px solid #b2b2b2;
  padding: 1rem;
}
.search__container {
  border: 1px solid #b2b2b2;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  background: #fff;
  color: #b2b2b2;
}
.search__input, .search__button {
  padding: 0.5rem;
  border: none;
}
.search__input {
  font-family: inherit;
  font-size: inherit;
  color: #6e6e6e;
  min-width: 300px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
}
.search__input::placeholder {
  color: #b2b2b2;
}
.search__input:focus::placeholder {
  color: #7477bf;
}
.search__button {
  background-color: #fff;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border: none;
  outline: none;
  transition: 0.4s;
}
.search__button:hover, .search__button:focus, .search__button:active {
  background-color: #b2b2b2;
}
.search__mag-icon {
  height: 1.5rem;
  width: 1.5rem;
  color: #7477bf;
  cursor: pointer;
  transition: color 0.4s;
}
.search__button:hover .search__mag-icon, .search__button:focus .search__mag-icon {
  color: #fff;
}
@media only screen and (min-width: 768px) {
  .search {
    flex: 5;
    border-top: none;
    border-left: 1px solid #b2b2b2;
    justify-content: flex-end;
    width: auto;
  }
}

.chart-container {
  position: relative;
}

.bar-chart,
.donut-chart,
.line-chart {
  padding: 1rem 0.5rem;
  grid-column: main-start/main-end;
}

.bar-chart,
.donut-chart {
  border-bottom: 1px solid #b2b2b2;
}

.bar-chart__chart {
  margin: 10px auto;
  margin-top: 20px;
}

.donut-chart__chart {
  margin: 20px auto;
}

.line-chart {
  border-bottom: 1px solid #b2b2b2;
}
.line-chart__chart {
  margin: 10px auto;
  margin-top: 20px;
}
.line-chart__time-options {
  list-style: none;
  font-size: 0.6rem;
  font-family: inherit;
  display: flex;
  justify-content: flex-end;
  margin-top: -20px;
}
@media only screen and (min-width: 768px) {
  .line-chart__time-options {
    font-size: 0.8rem;
  }
}
.line-chart__list-item a:link, .line-chart__list-item a:visited {
  margin-left: 0.5rem;
  padding: 0.3rem 1rem;
  border-radius: 35px;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  transition: 0.4s;
}
.line-chart__list-item a:hover {
  background-color: rgba(116, 177, 191, 0.3);
}
.line-chart__list-item--selected {
  background-color: #81c98f !important;
  color: #fff !important;
}

@media only screen and (min-width: 768px) {
  .line-chart {
    padding: 1rem 1rem;
  }
  .bar-chart {
    grid-column: main-start/main-middle;
    border-right: 1px solid #b2b2b2;
    padding: 1rem 1rem;
  }
  .donut-chart {
    grid-column: main-middle/main-end;
    padding: 1rem 1rem;
  }
}
@media only screen and (min-width: 1024px) {
  .line-chart__chart {
    width: 929px;
    height: 464px;
  }
}
.social {
  grid-column: main-start/main-end;
  display: flex;
  flex-direction: column;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #b2b2b2;
}
.social__title {
  margin-bottom: 1rem;
}
.social__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.social__box {
  flex: 0 0 12.5rem;
  margin: 0.5rem;
  padding: 1rem;
  border: 2px solid #b2b2b2;
  border-radius: 5px;
  background-color: #fbfbfb;
  display: flex;
}
.social__icon {
  margin-right: 0.5rem;
  background-color: #7477bf;
  border-radius: 50%;
  height: 65px;
  width: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__twitter {
  height: 32px;
  width: 32px;
  margin-bottom: -5px;
}
.social__facebook {
  height: 40px;
  width: 40px;
  margin-bottom: 5px;
}
.social__google {
  height: 32px;
  width: 32px;
}
.social__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.social__company {
  margin-bottom: -7px;
  color: #7477bf;
  font-weight: 700;
}
.social__figure {
  font-size: 1.8rem;
  font-weight: 300;
}
@media only screen and (min-width: 768px) {
  .social {
    padding: 1rem 1rem;
  }
  .social__container {
    justify-content: space-between;
  }
  .social__box {
    flex: 1 0 12.5rem;
    margin: 0;
  }
  .social__box:not(:last-child) {
    margin-right: 1rem;
  }
}

.new-members {
  grid-column: main-start/main-end;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #b2b2b2;
}
.new-members__title {
  text-transform: uppercase;
  margin-bottom: 1rem;
}
@media only screen and (min-width: 768px) {
  .new-members {
    grid-column: main-start/main-middle;
    border-right: 1px solid #b2b2b2;
    padding: 1rem 1rem;
  }
}

.recent-activity {
  grid-column: main-start/main-end;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #b2b2b2;
}
.recent-activity__title {
  text-transform: uppercase;
  margin-bottom: 1rem;
}
@media only screen and (min-width: 768px) {
  .recent-activity {
    grid-column: main-middle/main-end;
    padding: 1rem 1rem;
  }
}

.messages {
  grid-column: main-start/main-end;
  padding: 1rem 0.5rem;
  border-bottom: 1px solid #b2b2b2;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}
.messages__input {
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.messages__alert-window {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #7377bf;
  color: #fff;
  padding: 0.5rem;
  width: 90%;
  border-radius: 5px;
  transform: translateY(-110%) translateX(-50%);
  opacity: 1;
  transition: all 0.4s;
}
.messages__alert-window--show {
  transform: translateY(0) translateX(-50%);
}
.messages__title {
  margin-bottom: 1rem;
}
.messages__search, .messages__message {
  font-family: inherit;
  border: 1px solid #b2b2b2;
  padding: 0.5rem;
  border-radius: 5px;
}
.messages__search, .messages__message {
  background-color: #fbfbfb;
}
.messages__search::placeholder, .messages__message::placeholder {
  color: #b2b2b2;
}
.messages__search {
  margin-bottom: 0.5rem;
}
.messages__message {
  resize: none;
}
.messages__btn {
  background-color: #7477bf;
  transition: 0.4s;
  margin: 1rem 0;
}
.messages__btn:hover {
  background-color: #4d4c72;
}
@media only screen and (min-width: 768px) {
  .messages {
    grid-column: main-start/main-middle;
    border-right: 1px solid #b2b2b2;
    border-bottom: none;
    padding: 1rem 1rem;
  }
}

.settings {
  grid-column: main-start/main-end;
  padding: 1rem 0.5rem;
  display: flex;
  flex-direction: column;
}
.settings__input {
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.settings__title {
  margin-bottom: 1rem;
}
.settings__switch {
  margin-bottom: 0.5rem;
}
.settings__cta {
  display: flex;
  margin: 1rem 0;
}
.settings__save {
  background-color: #7477bf;
  margin-right: 0.5rem;
  flex: 1;
  transition: 0.4s;
}
.settings__save:hover, .settings__save:active {
  background-color: #4d4c72;
}
.settings__cancel {
  background-color: #b2b2b2;
  margin-left: 0.5rem;
  flex: 1;
  transition: 0.4s;
}
.settings__cancel:hover, .settings__cancel:active {
  background-color: #666666;
}
@media only screen and (min-width: 768px) {
  .settings {
    grid-column: main-middle/main-end;
    padding: 1rem 1rem;
  }
}